import React from "react";
import Head from "~/components/head/index";
import { connect } from "react-redux";
import { getCinemaList } from "~/actions/index";
import CinemaList from "~/components/cinemalist/index";
import "./index.scss";

const mapStateToProps = (state) => {
    return {
        cinemaList: state.getIn(["data", "cinemaList"])
    }
}

class Cinema extends React.Component {
    componentDidMount() {
        this.props.dispatch(getCinemaList({ url: "/react/getCinemaList" }))
    }

    render() {
        const {
            cinemaList
        } = this.props;

        return (
            <div>
                <Head title="影院" show={false} search={true} pop={true} />
                <CinemaList cinemaList={cinemaList} />
            </div>
        )
    }
}

export default connect(mapStateToProps)(Cinema)