import React from "react";
import "./index.scss";
import { history } from "&/history";

export default class CinemaList extends React.Component {
    gotoCinemaDetail = (cid, cname) => {
        history.push("/cinemaDetail/" + cid + "/?cname=" + cname)
    }

    render() {
        const {
            cinemaList
        } = this.props;
        return (
            <ul className="cinema-ul">
                {
                    cinemaList.map((item, i) => {
                        return (
                            <li key={i} className="cinema-li" onClick={() => this.gotoCinemaDetail(item.id, item.nm)}>
                                <ol>
                                    <li className="nm-li">
                                        <span className="nm-span">{item.nm}</span>
                                        <span className="price-span">{item.sellPrice}元起</span>
                                    </li>
                                    <li className="addr-li">{item.addr}</li>
                                    <li className="tag-li">
                                        {
                                            (item.tag.allowRefund == 1) && <p className="refund">退票</p>
                                        }
                                        {
                                            (item.tag.endorse == 1) && <p className="endorse">改签</p>
                                        }
                                        {
                                            (item.tag.snack == 1) && <p className="snack">小吃</p>
                                        }
                                        {
                                            (item.tag.vipTag) && <p className="vipCard">{item.tag.vipTag}</p>
                                        }
                                        {
                                            (item.tag.hallTypeVOList)
                                            && item.tag.hallTypeVOList.map((val, index) => {
                                                return (
                                                    <p key={index} className="hall">{val.name}</p>
                                                )
                                            })
                                        }
                                    </li>
                                    {
                                        (item.promotion) &&
                                        <li className="card-li">
                                            <p className="card">卡</p>
                                            <p className="tip">{item.promotion.cardPromotionTag}</p>
                                        </li>
                                    }
                                </ol>
                            </li>
                        )
                    })
                }
            </ul>
        )
    }
}